export default {
	data() {
		return {
			order:{},
			addressData: {},
		}
	},
	onShow() {
	},
	onLoad(option){
		this.initData(option);
	},
	methods: {
		initData(option){
			uni.request({
				url: this.$Url + '/clmsj/order/getOrderDetails',
				data: {
					id : option.id
				},
				header:{	'auth' : uni.getStorageSync('session_key')	},
				success: (res) => {
					console.log(res.data.data);
					this.order = res.data.data;
					let {stateTip, stateTipColor} = this.orderStateExp(this.order.state);
					console.log({stateTip, stateTipColor})
					console.log(this.order)
					this.order = Object.assign(this.order, {
						stateTip, 
						stateTipColor
					}) 
					console.log(this.order)
				}
			});
		},
		//查看物流
		viewExpress(item){
			console.log('viewExpress')
			uni.navigateToMiniProgram({
				appId: 'wx6885acbedba59c14',
				path: 'pages/result/result?nu='+ item.express_number +'&com=&querysource=third_xcx',
				success(res) {
					// 打开成功
				}
			})
		},
		//订单状态文字和颜色
		orderStateExp(state){
			let stateTip = '',
				stateTipColor = '#ffffff';
			switch(+state){
				case 0:
					stateTip = '待付款'; break;
				case 1:
					stateTip = '待付款'; break;
				case 2:
					stateTip = '已付款，待发货'; break;
				case 3:
					stateTip = '已发货，待收货'; break;
				case 4:
					stateTip = '已收货'; break;
				case 9:
					stateTip = '订单已关闭'; 
					stateTipColor = '#ffffff';
					break;
			}
			return {stateTip, stateTipColor};
		}
	}
}
